import React, { useState, useMemo, useEffect } from "react";
import { Box, VStack, HStack, Button, SimpleGrid } from "@chakra-ui/react";
import { CustomToggleButton } from "components/CustomToggleButton";
import { CustomNumberInput } from "components/CustomNumberInput";

// Define the complex shape of this section's data
interface FloorTileInstallDetails {
  type: "tile" | "lvp" | "carpet" | "engineered";
  tile: { [key: string]: string | number };
  lvp: { [key: string]: string | number };
  carpet: { sqft: string | number };
  engineered: { [key: string]: string | number };
  toggles: { [key: string]: boolean };
}

interface SectionProps {
  initialData: any;
  onUpdate: (data: object, totalCost: number) => void;
}

export const FloorTileInstallSection: React.FC<SectionProps> = ({
  initialData,
  onUpdate,
}) => {
  const [details, setDetails] = useState<FloorTileInstallDetails>(
    initialData?.floorTileInstallDetails || {
      type: "tile",
      tile: {
        floor: "",
        backsplash: "",
        wainscot: "",
        showerFloor: "",
        showerWall: "",
      },
      lvp: { sqft: "", t_mold_qty: "", end_mold_qty: "", stair_nose_qty: "" },
      carpet: { sqft: "" },
      engineered: {
        sqft: "",
        t_mold_qty: "",
        end_mold_qty: "",
        stair_nose_qty: "",
      },
      toggles: { niche: false, cornerShelf: false, bench: false },
    }
  );

  const totalCost = useMemo(() => {
    let cost = 0;
    const { type, tile, lvp, carpet, engineered, toggles } = details;
    switch (type) {
      case "tile":
        cost += (Number(tile.floor) || 0) * 5;
        cost += (Number(tile.backsplash) || 0) * 15;
        cost += (Number(tile.wainscot) || 0) * 15;
        cost += (Number(tile.showerFloor) || 0) * 45;
        cost += (Number(tile.showerWall) || 0) * 15;
        if (toggles.niche) cost += 150;
        if (toggles.cornerShelf) cost += 50;
        if (toggles.bench) cost += 100;
        break;
      case "lvp":
        cost += (Number(lvp.sqft) || 0) * 3;
        cost += (Number(lvp.t_mold_qty) || 0) * 45;
        cost += (Number(lvp.end_mold_qty) || 0) * 45;
        cost += (Number(lvp.stair_nose_qty) || 0) * 75;
        break;
      case "carpet":
        cost += ((Number(carpet.sqft) || 0) / 9) * 8; // Assumes cost is per sqyd
        break;
      case "engineered":
        cost += (Number(engineered.sqft) || 0) * 7;
        cost += (Number(engineered.t_mold_qty) || 0) * 45;
        cost += (Number(engineered.end_mold_qty) || 0) * 45;
        cost += (Number(engineered.stair_nose_qty) || 0) * 75;
        break;
      default:
        break;
    }
    return cost;
  }, [details]);

  useEffect(() => {
    onUpdate({ floorTileInstallDetails: details }, totalCost);
  }, [totalCost, details, onUpdate]);

  const handleTypeChange = (type: FloorTileInstallDetails["type"]) => {
    setDetails((prev) => ({ ...prev, type }));
  };

  const handleInputChange = (subField: string, value: string) => {
    const currentType = details.type;
    setDetails((prev) => ({
      ...prev,
      [currentType]: { ...prev[currentType], [subField]: value },
    }));
  };

  const handleToggleClick = (field: string) => {
    setDetails((prev) => ({
      ...prev,
      toggles: { ...prev.toggles, [field]: !prev.toggles[field] },
    }));
  };

  return (
    <Box pt={2}>
      <VStack spacing={6} align="stretch">
        <SimpleGrid columns={{ base: 2, md: 4 }} spacing={2}>
          {(["tile", "lvp", "carpet", "engineered"] as const).map((type) => (
            <Button
              key={type}
              onClick={() => handleTypeChange(type)}
              size="sm"
              colorScheme={details.type === type ? "blue" : "gray"}
              textTransform="capitalize"
            >
              {type}
            </Button>
          ))}
        </SimpleGrid>

        {details.type === "tile" && (
          <VStack spacing={4} align="stretch">
            <CustomNumberInput
              label="Floor Tile"
              value={details.tile.floor}
              onChange={(e) => handleInputChange("floor", e.target.value)}
              unit="sqft"
              subtext="$5/sqft"
            />
            <CustomNumberInput
              label="Backsplash"
              value={details.tile.backsplash}
              onChange={(e) => handleInputChange("backsplash", e.target.value)}
              unit="sqft"
              subtext="$15/sqft"
            />
            <CustomNumberInput
              label="Wainscot"
              value={details.tile.wainscot}
              onChange={(e) => handleInputChange("wainscot", e.target.value)}
              unit="sqft"
              subtext="$15/sqft"
            />
            <CustomNumberInput
              label="Shower Floor"
              value={details.tile.showerFloor}
              onChange={(e) => handleInputChange("showerFloor", e.target.value)}
              unit="sqft"
              subtext="$45/sqft"
            />
            <CustomNumberInput
              label="Shower Wall"
              value={details.tile.showerWall}
              onChange={(e) => handleInputChange("showerWall", e.target.value)}
              unit="sqft"
              subtext="$15/sqft"
            />
            <HStack>
              <CustomToggleButton
                label="Niche"
                cost={150}
                isSelected={details.toggles.niche}
                onClick={() => handleToggleClick("niche")}
              />
              <CustomToggleButton
                label="Corner Shelf"
                cost={50}
                isSelected={details.toggles.cornerShelf}
                onClick={() => handleToggleClick("cornerShelf")}
              />
              <CustomToggleButton
                label="Bench"
                cost={100}
                isSelected={details.toggles.bench}
                onClick={() => handleToggleClick("bench")}
              />
            </HStack>
          </VStack>
        )}

        {(details.type === "lvp" || details.type === "engineered") && (
          <VStack spacing={4} align="stretch">
            <CustomNumberInput
              label={`${details.type.toUpperCase()} Labor`}
              value={details[details.type].sqft}
              onChange={(e) => handleInputChange("sqft", e.target.value)}
              unit="sqft"
              subtext={details.type === "lvp" ? "$3/sqft" : "$7/sqft"}
            />
            <CustomNumberInput
              label="T-Mold"
              value={details[details.type].t_mold_qty}
              onChange={(e) => handleInputChange("t_mold_qty", e.target.value)}
              unit="qty"
              subtext="$45/each"
            />
            <CustomNumberInput
              label="End Mold"
              value={details[details.type].end_mold_qty}
              onChange={(e) =>
                handleInputChange("end_mold_qty", e.target.value)
              }
              unit="qty"
              subtext="$45/each"
            />
            <CustomNumberInput
              label="Stair Nose"
              value={details[details.type].stair_nose_qty}
              onChange={(e) =>
                handleInputChange("stair_nose_qty", e.target.value)
              }
              unit="qty"
              subtext="$75/each"
            />
          </VStack>
        )}

        {details.type === "carpet" && (
          <CustomNumberInput
            label="Carpet"
            value={details.carpet.sqft}
            onChange={(e) => handleInputChange("sqft", e.target.value)}
            unit="sqft"
            subtext="$8/sqyd"
          />
        )}
      </VStack>
    </Box>
  );
};
