import React, { useState, useMemo, useEffect } from "react";
import { Box, VStack } from "@chakra-ui/react";
import { CustomNumberInput } from "components/CustomNumberInput";

// Define the shape of this section's data
interface HVACDetails {
  relocateRegister: string | number;
  miniSplit: string | number;
}

// Define the props this section will receive
interface SectionProps {
  initialData: any;
  onUpdate: (data: object, totalCost: number) => void;
}

export const HVACSection: React.FC<SectionProps> = ({
  initialData,
  onUpdate,
}) => {
  const [details, setDetails] = useState<HVACDetails>(
    initialData?.hvacDetails || {
      relocateRegister: "",
      miniSplit: "",
    }
  );

  const totalCost = useMemo(() => {
    const relocateCost = Number(details.relocateRegister) || 0;
    const miniSplitCost = Number(details.miniSplit) || 0;
    return relocateCost + miniSplitCost;
  }, [details]);

  // Send data up to the parent page whenever it changes
  useEffect(() => {
    onUpdate({ hvacDetails: details }, totalCost);
  }, [totalCost, details, onUpdate]);

  const handleInputChange = (field: keyof HVACDetails, value: string) => {
    setDetails((prev) => ({ ...prev, [field]: value }));
  };

  return (
    <Box pt={2}>
      <VStack spacing={6} align="stretch">
        <CustomNumberInput
          label="Relocate Register"
          unit="$"
          value={details.relocateRegister}
          onChange={(e) =>
            handleInputChange("relocateRegister", e.target.value)
          }
        />
        <CustomNumberInput
          label="Mini-Split"
          unit="$"
          value={details.miniSplit}
          onChange={(e) => handleInputChange("miniSplit", e.target.value)}
        />
      </VStack>
    </Box>
  );
};
