import React, { useState, useRef } from "react";
import {
  Container,
  Heading,
  Text,
  SimpleGrid,
  Box,
  Icon,
  VStack,
  Button,
  Modal,
  ModalOverlay,
  ModalContent,
  ModalHeader,
  ModalFooter,
  ModalBody,
  ModalCloseButton,
  FormControl,
  FormLabel,
  Input,
  useDisclosure,
  AlertDialog,
  AlertDialogBody,
  AlertDialogFooter,
  AlertDialogHeader,
  AlertDialogContent,
  AlertDialogOverlay,
} from "@chakra-ui/react";
import {
  Home,
  Bath,
  ShowerHead,
  CookingPot,
  Save,
  FolderOpen,
  RefreshCcw,
} from "lucide-react";

interface ActionButtonProps {
  name: string;
  icon: React.ElementType;
  onClick: () => void;
}
const ActionButton: React.FC<ActionButtonProps> = ({ name, icon, onClick }) => {
  return (
    <Box
      as="button"
      onClick={onClick}
      bg="gray.100"
      p={6}
      borderRadius="xl"
      boxShadow="md"
      textAlign="center"
      transition="all 0.3s"
      _hover={{
        bg: "gray.200",
        boxShadow: "lg",
        transform: "scale(1.05)",
      }}
    >
      <VStack spacing={4}>
        <Icon as={icon} boxSize={10} color="gray.600" />
        <Heading size="md" fontWeight="semibold" color="gray.700">
          {name}
        </Heading>
      </VStack>
    </Box>
  );
};

interface Category {
  name: string;
  icon: React.ElementType;
  page: string;
}
interface CategoryButtonProps {
  category: Category;
  onNavigate: (page: string) => void;
  total?: number;
}
const CategoryButton: React.FC<CategoryButtonProps> = ({
  category,
  onNavigate,
  total = 0,
}) => {
  const hasTotal = total > 0;
  return (
    <Box
      as="button"
      onClick={() => onNavigate(category.page)}
      bg={hasTotal ? "blue.500" : "white"}
      color={hasTotal ? "white" : "inherit"}
      p={6}
      borderRadius="xl"
      boxShadow="md"
      textAlign="center"
      transition="all 0.3s"
      _hover={{
        bg: "blue.500",
        color: "white",
        boxShadow: "xl",
        transform: "scale(1.05)",
      }}
    >
      <VStack spacing={4}>
        <Icon
          as={category.icon}
          boxSize={10}
          color={hasTotal ? "white" : "blue.500"}
        />
        <VStack spacing={1}>
          <Heading size="md" fontWeight="semibold">
            {category.name}
          </Heading>
          {hasTotal && (
            <Text fontWeight="bold" fontSize="sm">
              $
              {total.toLocaleString("en-US", {
                minimumFractionDigits: 2,
                maximumFractionDigits: 2,
              })}
            </Text>
          )}
        </VStack>
      </VStack>
    </Box>
  );
};

interface HomePageProps {
  onNavigate: (page: string) => void;
  totals: { [key: string]: number };
  onSave: (name: string) => void;
  onLoad: (data: any) => void;
  onClearCurrent: () => void;
}

const HomePage: React.FC<HomePageProps> = ({
  onNavigate,
  totals,
  onSave,
  onLoad,
  onClearCurrent,
}) => {
  const [isSaveModalOpen, setSaveModalOpen] = useState(false);
  const [estimateName, setEstimateName] = useState("");
  const {
    isOpen: isClearAlertOpen,
    onOpen: onClearAlertOpen,
    onClose: onClearAlertClose,
  } = useDisclosure();
  const cancelRef = useRef(null);

  const fileInputRef = useRef<HTMLInputElement>(null);

  const categories: Category[] = [
    { name: "General Remodel", icon: Home, page: "general-remodel" },
    { name: "Master Bathroom", icon: Bath, page: "master-bathroom" },
    { name: "Guest Bathroom", icon: ShowerHead, page: "guest-bathroom" },
    { name: "Kitchen", icon: CookingPot, page: "kitchen" },
  ];

  const handleSaveClick = () => {
    onSave(estimateName);
    setEstimateName("");
    setSaveModalOpen(false);
  };

  const handleFileSelect = (event: React.ChangeEvent<HTMLInputElement>) => {
    const file = event.target.files?.[0];
    if (!file) return;

    const reader = new FileReader();
    reader.onload = (e) => {
      try {
        const text = e.target?.result;
        const data = JSON.parse(text as string);
        onLoad(data);
      } catch (error) {
        console.error("Failed to read or parse file:", error);
        alert(
          "Could not load the estimate. The file may be corrupt or not a valid estimate file."
        );
      }
    };
    reader.readAsText(file);
    // Reset the input value so the onChange event fires even if you select the same file again
    event.target.value = "";
  };

  const handleLoadClick = () => {
    fileInputRef.current?.click();
  };

  return (
    <>
      <input
        type="file"
        accept=".json"
        style={{ display: "none" }}
        ref={fileInputRef}
        onChange={handleFileSelect}
      />
      <Container maxW="container.lg" py={12}>
        <VStack spacing={10}>
          <Box textAlign="center">
            <Heading as="h1" size="2xl" color="gray.800">
              Estimator Pro
            </Heading>
            <Text fontSize="xl" color="gray.600" mt={2}>
              Choose a template or load a project
            </Text>
          </Box>
          <SimpleGrid columns={{ base: 1, md: 2, lg: 3 }} spacing={6} w="100%">
            {categories.map((category) => {
              const keyMap: { [key: string]: string } = {
                "General Remodel": "generalRemodel",
                "Master Bathroom": "masterBathroom",
                "Guest Bathroom": "guestBathroom",
                Kitchen: "kitchen",
              };
              const total = totals[keyMap[category.name]] || 0;

              return (
                <CategoryButton
                  key={category.name}
                  category={category}
                  onNavigate={onNavigate}
                  total={total}
                />
              );
            })}
            <ActionButton
              name="Save Estimate"
              icon={Save}
              onClick={() => setSaveModalOpen(true)}
            />
            <ActionButton
              name="Load Estimate"
              icon={FolderOpen}
              onClick={handleLoadClick}
            />
          </SimpleGrid>
          <Button
            variant="outline"
            colorScheme="orange"
            leftIcon={<RefreshCcw size={16} />}
            onClick={onClearAlertOpen}
            mt={4}
          >
            Clear Current / Start New
          </Button>
          <Text fontSize="sm" color="gray.500" pt={8}>
            &copy; {new Date().getFullYear()} Estimator Pro. All rights
            reserved.
          </Text>
        </VStack>
      </Container>

      {/* Save Estimate Modal */}
      <Modal
        isOpen={isSaveModalOpen}
        onClose={() => setSaveModalOpen(false)}
        isCentered
      >
        <ModalOverlay />
        <ModalContent>
          <ModalHeader>Save Estimate</ModalHeader>
          <ModalCloseButton />
          <ModalBody>
            <FormControl>
              <FormLabel>Estimate Name</FormLabel>
              <Input
                placeholder="e.g., 'John Smith - Kitchen'"
                value={estimateName}
                onChange={(e) => setEstimateName(e.target.value)}
                autoFocus
              />
            </FormControl>
          </ModalBody>
          <ModalFooter>
            <Button
              variant="ghost"
              mr={3}
              onClick={() => setSaveModalOpen(false)}
            >
              Cancel
            </Button>
            <Button
              colorScheme="blue"
              onClick={handleSaveClick}
              isDisabled={!estimateName}
            >
              Save
            </Button>
          </ModalFooter>
        </ModalContent>
      </Modal>

      {/* Confirmation Dialog for Clearing Data */}
      <AlertDialog
        isOpen={isClearAlertOpen}
        leastDestructiveRef={cancelRef}
        onClose={onClearAlertClose}
        isCentered
      >
        <AlertDialogOverlay>
          <AlertDialogContent>
            <AlertDialogHeader fontSize="lg" fontWeight="bold">
              Delete All Estimates
            </AlertDialogHeader>
            <AlertDialogBody>
              Are you sure? All unsaved work in your current estimate will be
              reset to zero. This will not affect your saved projects.
            </AlertDialogBody>
            <AlertDialogFooter>
              <Button ref={cancelRef} onClick={onClearAlertClose}>
                Cancel
              </Button>
              <Button
                colorScheme="orange"
                onClick={() => {
                  onClearCurrent();
                  onClearAlertClose();
                }}
                ml={3}
              >
                Yes, Clear
              </Button>
            </AlertDialogFooter>
          </AlertDialogContent>
        </AlertDialogOverlay>
      </AlertDialog>
    </>
  );
};

export default HomePage;
