import React, { useState, useMemo, useEffect } from "react";
import { Box, VStack } from "@chakra-ui/react";
import { CustomNumberInput } from "components/CustomNumberInput";

// Define the shape of this section's data
interface InsulationDetails {
  radiantBarrier: string | number;
  blownIn: string | number;
  batt: string | number;
}

// Define the props this section will receive
interface SectionProps {
  initialData: any;
  onUpdate: (data: object, totalCost: number) => void;
}

export const InsulationSection: React.FC<SectionProps> = ({
  initialData,
  onUpdate,
}) => {
  const [details, setDetails] = useState<InsulationDetails>(
    initialData?.insulationDetails || {
      radiantBarrier: "",
      blownIn: "",
      batt: "",
    }
  );

  const totalCost = useMemo(() => {
    const radiantCost = Number(details.radiantBarrier) || 0;
    const blownInCost = Number(details.blownIn) || 0;
    const battCost = Number(details.batt) || 0;
    return radiantCost + blownInCost + battCost;
  }, [details]);

  // Send data up to the parent page whenever it changes
  useEffect(() => {
    onUpdate({ insulationDetails: details }, totalCost);
  }, [totalCost, details, onUpdate]);

  const handleInputChange = (field: keyof InsulationDetails, value: string) => {
    setDetails((prev) => ({ ...prev, [field]: value }));
  };

  return (
    <Box pt={2}>
      <VStack spacing={6} align="stretch">
        <CustomNumberInput
          label="Radiant Barrier"
          unit="$"
          value={details.radiantBarrier}
          onChange={(e) => handleInputChange("radiantBarrier", e.target.value)}
        />
        <CustomNumberInput
          label="Blown-In"
          unit="$"
          value={details.blownIn}
          onChange={(e) => handleInputChange("blownIn", e.target.value)}
        />
        <CustomNumberInput
          label="Batt"
          unit="$"
          value={details.batt}
          onChange={(e) => handleInputChange("batt", e.target.value)}
        />
      </VStack>
    </Box>
  );
};
