import React, { useState, useMemo, useEffect } from "react";
import { Box, VStack, Text, Button, Flex, Divider } from "@chakra-ui/react";

interface PermitFeeDetails {
  isPermitIncluded: boolean;
  isPercentageApplied: boolean;
}

interface SectionProps {
  subtotal: number;
  initialData: any;
  onUpdate: (data: object, totalCost: number) => void;
}

export const PermitFeeSection: React.FC<SectionProps> = ({
  subtotal,
  initialData,
  onUpdate,
}) => {
  const [details, setDetails] = useState<PermitFeeDetails>(
    initialData?.permitFeeDetails || {
      isPermitIncluded: false,
      isPercentageApplied: false,
    }
  );

  const percentageAmount = useMemo(() => subtotal * 0.02, [subtotal]);
  const baseFee = 350;

  const totalCost = useMemo(() => {
    if (!details.isPermitIncluded) return 0;
    return baseFee + (details.isPercentageApplied ? percentageAmount : 0);
  }, [details.isPermitIncluded, details.isPercentageApplied, percentageAmount]);

  useEffect(() => {
    onUpdate({ permitFeeDetails: details }, totalCost);
  }, [totalCost, details, onUpdate]);

  const togglePermitInclusion = () => {
    const isNowIncluded = !details.isPermitIncluded;
    // If we are removing the permit, also remove the percentage fee
    setDetails((prev) => ({
      ...prev,
      isPermitIncluded: isNowIncluded,
      isPercentageApplied: isNowIncluded ? prev.isPercentageApplied : false,
    }));
  };

  const togglePercentage = () => {
    setDetails((prev) => ({
      ...prev,
      isPercentageApplied: !prev.isPercentageApplied,
    }));
  };

  return (
    <Box pt={2}>
      <VStack spacing={4} align="stretch">
        <Button
          onClick={togglePermitInclusion}
          colorScheme={details.isPermitIncluded ? "red" : "green"}
        >
          {details.isPermitIncluded
            ? "Remove Permit Fee"
            : "Include Permit Fee"}
        </Button>

        {details.isPermitIncluded && (
          <VStack
            p={4}
            bg="gray.100"
            borderRadius="md"
            align="stretch"
            spacing={4}
          >
            <Flex justify="space-between" align="center">
              <Text>Base Permit Fee:</Text>
              <Text fontWeight="bold">${baseFee.toFixed(2)}</Text>
            </Flex>
            <Divider />
            <Flex justify="space-between" align="center">
              <Text>2% of Subtotal (${subtotal.toLocaleString()}):</Text>
              <Text fontWeight="bold">${percentageAmount.toFixed(2)}</Text>
            </Flex>
            <Button
              onClick={togglePercentage}
              colorScheme={details.isPercentageApplied ? "red" : "green"}
              size="sm"
            >
              {details.isPercentageApplied ? "Remove 2% Fee" : "Add 2% Fee"}
            </Button>
          </VStack>
        )}
      </VStack>
    </Box>
  );
};
