import React, { useState, useMemo, useEffect } from "react";
import { Box, VStack } from "@chakra-ui/react";
import { CustomToggleButton } from "components/CustomToggleButton";
import { CustomNumberInput } from "components/CustomNumberInput";

// Define the shape of this section's data
interface CabinetsDetails {
  lnft: string | number;
  economyVanity: boolean;
}

// Define the props this section will receive
interface SectionProps {
  initialData: any;
  onUpdate: (data: object, totalCost: number) => void;
}

export const CabinetsSection: React.FC<SectionProps> = ({
  initialData,
  onUpdate,
}) => {
  const [details, setDetails] = useState<CabinetsDetails>(
    initialData?.cabinetsDetails || {
      lnft: "",
      economyVanity: false,
    }
  );

  const totalCost = useMemo(() => {
    const cabinetCost = (Number(details.lnft) || 0) * 800;
    const vanityCost = details.economyVanity ? 3200 : 0;
    return cabinetCost + vanityCost;
  }, [details]);

  // Send data up to the parent page whenever it changes
  useEffect(() => {
    onUpdate({ cabinetsDetails: details }, totalCost);
  }, [totalCost, details, onUpdate]);

  const handleInputChange = (field: keyof CabinetsDetails, value: string) => {
    setDetails((prev) => ({ ...prev, [field]: value }));
  };

  const handleToggleClick = (field: "economyVanity") => {
    setDetails((prev) => ({ ...prev, [field]: !prev[field] }));
  };

  return (
    <Box pt={2}>
      <VStack spacing={6} align="stretch">
        <CustomNumberInput
          label="Cabinets"
          unit="lnft"
          subtext="$800/lnft"
          value={details.lnft}
          onChange={(e) => handleInputChange("lnft", e.target.value)}
        />
        <CustomToggleButton
          label="Economy Vanity"
          cost={3200}
          isSelected={details.economyVanity}
          onClick={() => handleToggleClick("economyVanity")}
        />
      </VStack>
    </Box>
  );
};
