import React, { useState, useMemo, useEffect } from "react";
import { Box, VStack, Checkbox } from "@chakra-ui/react";

// Define the new shape of this section's data using booleans
interface EngineeringDetails {
  needsSurvey: boolean;
  needsDrafting: boolean;
  needsEngineering: boolean;
}

// The props this section receives remain the same
interface SectionProps {
  initialData: any;
  onUpdate: (data: object, totalCost: number) => void;
}

export const PlansAndEngineeringSection: React.FC<SectionProps> = ({
  initialData,
  onUpdate,
}) => {
  // The state now just tracks if the boxes are checked
  const [details, setDetails] = useState<EngineeringDetails>(
    initialData?.plansAndEngineeringDetails || {
      needsSurvey: false,
      needsDrafting: false,
      needsEngineering: false,
    }
  );

  // This section no longer contributes to the cost, so its total is always 0
  const totalCost = 0;

  // We still send the data up to be saved, even though the cost is zero
  useEffect(() => {
    // The data we save is now about which boxes are checked
    onUpdate({ plansAndEngineeringDetails: details }, totalCost);
  }, [details, onUpdate]);

  const handleCheckboxChange = (field: keyof EngineeringDetails) => {
    setDetails((prev) => ({ ...prev, [field]: !prev[field] }));
  };

  return (
    <Box pt={2}>
      <VStack spacing={4} align="stretch">
        <Checkbox
          isChecked={details.needsSurvey}
          onChange={() => handleCheckboxChange("needsSurvey")}
        >
          Survey May Be Required
        </Checkbox>
        <Checkbox
          isChecked={details.needsDrafting}
          onChange={() => handleCheckboxChange("needsDrafting")}
        >
          Plan Drafting May Be Required
        </Checkbox>
        <Checkbox
          isChecked={details.needsEngineering}
          onChange={() => handleCheckboxChange("needsEngineering")}
        >
          Engineering May Be Required
        </Checkbox>
      </VStack>
    </Box>
  );
};
