import React, { useState, useMemo, useEffect } from "react";
import {
  Box,
  VStack,
  HStack,
  SimpleGrid,
  Checkbox,
  Divider,
  Text,
  Input,
  Flex,
} from "@chakra-ui/react";
import { CustomToggleButton } from "../../../components/CustomToggleButton";

// Define the shape of this section's data
interface PlumbingDetails {
  toggles: { [key: string]: boolean };
  checklist: { [key: string]: boolean };
  vanitySinkCount: number | string;
}

// Define the props this section will receive
interface SectionProps {
  initialData: any;
  onUpdate: (data: object, totalCost: number) => void;
}

export const PlumbingSection: React.FC<SectionProps> = ({
  initialData,
  onUpdate,
}) => {
  const [details, setDetails] = useState<PlumbingDetails>(
    initialData?.plumbingDetails || {
      toggles: { master: false, guest: false, kitchen: false },
      checklist: {
        vanitySink: false,
        kitchenSink: false,
        toiletInstall: false,
        tubFiller: false,
        showerSystem: false,
        floFx: false,
        fridge: false,
        dishWasher: false,
        washingMachine: false,
      },
      vanitySinkCount: 0,
    }
  );

  const checklistItems = [
    { key: "kitchenSink", label: "Kitchen Sink", cost: 200 },
    { key: "toiletInstall", label: "Toilet Install", cost: 125 },
    { key: "tubFiller", label: "Tub Filler", cost: 450 },
    { key: "showerSystem", label: "Shower System", cost: 600 },
    { key: "floFx", label: "FloFX Drain", cost: 45 },
    { key: "fridge", label: "Fridge Line", cost: 150 },
    { key: "dishWasher", label: "Dish Washer", cost: 175 },
    { key: "washingMachine", label: "Washing Machine", cost: 100 },
  ];

  const totalCost = useMemo(() => {
    let c = 0;
    if (details.toggles.master) c += 3200;
    if (details.toggles.guest) c += 1900;
    if (details.toggles.kitchen) c += 1000;
    if (details.checklist.vanitySink)
      c += (Number(details.vanitySinkCount) || 0) * 150;

    checklistItems.forEach((item) => {
      if (details.checklist[item.key]) c += item.cost;
    });

    return c;
  }, [details, checklistItems]);

  useEffect(() => {
    onUpdate({ plumbingDetails: details }, totalCost);
  }, [totalCost, details, onUpdate]);

  const handleToggleClick = (type: string) => {
    setDetails((prev) => ({
      ...prev,
      toggles: { ...prev.toggles, [type]: !prev.toggles[type] },
    }));
  };

  const handleChecklistChange = (itemKey: string, isChecked: boolean) => {
    setDetails((prev) => ({
      ...prev,
      checklist: { ...prev.checklist, [itemKey]: isChecked },
    }));
  };

  const handleVanityCountChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const val = e.target.value;
    if (val === "" || (!isNaN(parseInt(val)) && parseInt(val) >= 0)) {
      setDetails((prev) => ({ ...prev, vanitySinkCount: parseInt(val) || 0 }));
    }
  };

  return (
    <Box pt={2}>
      <VStack spacing={6} align="stretch">
        <SimpleGrid columns={3} spacing={4}>
          <CustomToggleButton
            label="Master Bath"
            cost={3200}
            isSelected={details.toggles.master}
            onClick={() => handleToggleClick("master")}
          />
          <CustomToggleButton
            label="Guest Bath"
            cost={1900}
            isSelected={details.toggles.guest}
            onClick={() => handleToggleClick("guest")}
          />
          <CustomToggleButton
            label="Kitchen"
            cost={1000}
            isSelected={details.toggles.kitchen}
            onClick={() => handleToggleClick("kitchen")}
          />
        </SimpleGrid>
        <Divider />
        <Text fontWeight="bold">Installation Checklist</Text>
        <SimpleGrid columns={{ base: 1, md: 2 }} spacing={4}>
          <Flex
            align="center"
            justify="space-between"
            bg="gray.100"
            p={2}
            borderRadius="md"
          >
            <Checkbox
              isChecked={details.checklist.vanitySink}
              onChange={(e) =>
                handleChecklistChange("vanitySink", e.target.checked)
              }
            >
              Vanity Sink/s ($150/ea)
            </Checkbox>
            <Input
              type="number"
              value={details.vanitySinkCount}
              onChange={handleVanityCountChange}
              isDisabled={!details.checklist.vanitySink}
              w="80px"
              size="sm"
            />
          </Flex>
          {checklistItems.map((item) => (
            <Checkbox
              key={item.key}
              isChecked={details.checklist[item.key]}
              onChange={(e) =>
                handleChecklistChange(item.key, e.target.checked)
              }
              bg="gray.100"
              p={2}
              borderRadius="md"
            >
              {item.label} (${item.cost})
            </Checkbox>
          ))}
        </SimpleGrid>
      </VStack>
    </Box>
  );
};
