import React, { useState, useMemo, useEffect } from "react";
import { Box, Button, Slide } from "@chakra-ui/react";
import * as serviceWorkerRegistration from "./registerServiceWorker";
import HomePage from "./pages/HomePage";
import GeneralRemodelPage from "./pages/GeneralRemodel/GeneralRemodelPage";
import MasterBathroomPage from "./pages/MasterBathroom/MasterBathroomPage";
import GuestBathroomPage from "./pages/GuestBathroom/GuestBathroomPage";
import KitchenPage from "./pages/Kitchen/KitchenPage";

// We no longer import LoadPage, as it has been deleted.

const initialEstimateState = {
  generalRemodel: {},
  masterBathroom: {},
  guestBathroom: {},
  kitchen: {},
};

export default function App() {
  const [currentPage, setCurrentPage] = useState("home");
  const [estimateData, setEstimateData] = useState(initialEstimateState);

  const [showUpdate, setShowUpdate] = useState(false);
  const [waitingWorker, setWaitingWorker] = useState<ServiceWorker | null>(
    null
  );

  useEffect(() => {
    serviceWorkerRegistration.register({
      onUpdate: (registration) => {
        setWaitingWorker(registration.waiting);
        setShowUpdate(true);
      },
    });
  }, []);

  const handleUpdateAccepted = () => {
    waitingWorker?.postMessage({ type: "SKIP_WAITING" });
    setShowUpdate(false);
    setTimeout(() => {
      window.location.reload();
    }, 500);
  };

  const navigateTo = (page: string) => setCurrentPage(page);

  const handleSaveEstimate = (name: string) => {
    if (!name) {
      alert("Please enter a name for the estimate file.");
      return;
    }
    try {
      const jsonString = JSON.stringify(estimateData, null, 2);
      const blob = new Blob([jsonString], { type: "application/json" });
      const url = URL.createObjectURL(blob);
      const link = document.createElement("a");
      link.href = url;
      const fileName = name.endsWith(".json") ? name : `${name}.json`;
      link.download = fileName;
      document.body.appendChild(link);
      link.click();
      document.body.removeChild(link);
      URL.revokeObjectURL(url);
      alert(`Estimate "${name}" saved successfully!`);
    } catch (error) {
      console.error("Failed to save estimate:", error);
      alert("There was an error saving your estimate.");
    }
  };

  const handleLoadEstimate = (newData: any) => {
    if (newData && newData.generalRemodel) {
      setEstimateData(newData);
      alert("Estimate loaded successfully!");
    } else {
      alert("This does not appear to be a valid estimate file.");
    }
  };

  const handleClearCurrent = () => {
    if (window.confirm("Are you sure? All unsaved work will be lost.")) {
      setEstimateData(initialEstimateState);
      navigateTo("home");
      alert("Current estimate has been cleared.");
    }
  };

  const calculateTotal = (estimateSection: any) => {
    if (!estimateSection) return 0;
    const costs = estimateSection as { [key: string]: number };
    return Object.keys(costs)
      .filter((key) => key.endsWith("Cost"))
      .reduce((sum, key) => sum + (costs[key] || 0), 0);
  };

  const totals = {
    generalRemodel: useMemo(
      () => calculateTotal(estimateData.generalRemodel),
      [estimateData.generalRemodel]
    ),
    masterBathroom: useMemo(
      () => calculateTotal(estimateData.masterBathroom),
      [estimateData.masterBathroom]
    ),
    guestBathroom: useMemo(
      () => calculateTotal(estimateData.guestBathroom),
      [estimateData.guestBathroom]
    ),
    kitchen: useMemo(
      () => calculateTotal(estimateData.kitchen),
      [estimateData.kitchen]
    ),
  };

  const renderPage = () => {
    const pageProps = {
      onNavigate: navigateTo,
      estimateData,
      setEstimateData,
      onSave: handleSaveEstimate,
      onLoad: handleLoadEstimate,
      onClearCurrent: handleClearCurrent,
    };
    switch (currentPage) {
      case "home":
        return <HomePage {...pageProps} totals={totals} />;
      case "general-remodel":
        return <GeneralRemodelPage {...pageProps} />;
      case "master-bathroom":
        return <MasterBathroomPage {...pageProps} />;
      case "guest-bathroom":
        return <GuestBathroomPage {...pageProps} />;
      case "kitchen":
        return <KitchenPage {...pageProps} />;
      // --- FIX: The case for the deleted 'load' page has been removed ---
      default:
        return <HomePage {...pageProps} totals={totals} />;
    }
  };

  return (
    <Box position="relative" minH="100vh">
      <div className="font-sans">
        <style>{`
              input[type=number]::-webkit-inner-spin-button,
              input[type=number]::-webkit-outer-spin-button {
                  -webkit-appearance: none; margin: 0;
              }
              input[type=number] { -moz-appearance: textfield; }
          `}</style>
        {renderPage()}
      </div>

      <Slide direction="bottom" in={showUpdate} style={{ zIndex: 20 }}>
        <Box
          p="4"
          color="white"
          bg="blue.500"
          roundedTop="md"
          shadow="lg"
          textAlign="center"
        >
          A new version of the app is available.
          <Button
            colorScheme="whiteAlpha"
            variant="outline"
            size="sm"
            ml={4}
            onClick={handleUpdateAccepted}
          >
            Refresh
          </Button>
        </Box>
      </Slide>
    </Box>
  );
}
