import React, { useState, useMemo, useEffect } from "react";
import { Box, VStack } from "@chakra-ui/react";
import { CustomNumberInput } from "components/CustomNumberInput";

// Define the shape of this section's data
interface CountertopsDetails {
  sqft: string | number;
}

// Define the props this section will receive
interface SectionProps {
  initialData: any;
  onUpdate: (data: object, totalCost: number) => void;
}

export const CountertopsSection: React.FC<SectionProps> = ({
  initialData,
  onUpdate,
}) => {
  const [details, setDetails] = useState<CountertopsDetails>(
    initialData?.countertopsDetails || {
      sqft: "",
    }
  );

  const totalCost = useMemo(() => {
    return (Number(details.sqft) || 0) * 120;
  }, [details]);

  // Send data up to the parent page whenever it changes
  useEffect(() => {
    onUpdate({ countertopsDetails: details }, totalCost);
  }, [totalCost, details, onUpdate]);

  const handleInputChange = (
    field: keyof CountertopsDetails,
    value: string
  ) => {
    setDetails((prev) => ({ ...prev, [field]: value }));
  };

  return (
    <Box pt={2}>
      <VStack spacing={6} align="stretch">
        <CustomNumberInput
          label="Countertops"
          unit="sqft"
          subtext="$120/sqft"
          value={details.sqft}
          onChange={(e) => handleInputChange("sqft", e.target.value)}
        />
      </VStack>
    </Box>
  );
};
